import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {CenterComponent} from './center/center.component';
import {AboutUsComponent} from './about-us/about-us.component';
import {IAmASchoolComponent} from './i-am-a-school/i-am-a-school.component';
import {IAmAStudentComponent} from './i-am-a-student/i-am-a-student.component';
import {CareerComponent} from './career/career.component';
import {ContactUsComponent} from './contact-us/contact-us.component';

const routes: Routes = [
{
path:'',
component:CenterComponent
},
{
path:'about-us',
component:AboutUsComponent
}
,
{
path:'i-am-a-school',
component:IAmASchoolComponent
}
,
{
path:'i-am-a-student',
component:IAmAStudentComponent
}
,
{
path:'career',
component:CareerComponent
},
{
path:'contact-us',
component:ContactUsComponent
}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
